<template>
	<div>
		<div class="project">
			<ul>
				<li v-for="val in newsMsg">
					<a href="javascript:;">{{val.content | trim}}</a>
				</li>
			</ul>
		</div>
	</div>
</template>


<script type="text/javascript">
	export default {
		data() {
			return {
				newsMsg: []
			}
		},
		created() {
			this.fetchData();
		},
		methods: {
			fetchData: function() {
				this.$http.jsonp('https://bird.ioliu.cn/joke/rand', {
					params: {
						type: 'text',
						pagesize: 1
					},
				}).then(res => {
					console.log(res);
					this.newsMsg=res.body.data;
					
				}, err => {
					console.log(err);
				})
			}
		}

	}
</script>
<style type="text/css" scoped>
	.project ul li a{
		display: inline-block;
		width: 100%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

</style>


